import { Container } from 'react-bootstrap';
import { useEffect, useState } from "react";
import { useParams, Link } from "react-router-dom";
import { Abilities, Types, Stats } from "./Objects/Const";
import noFound from "./NoFound.png";

import { css } from "@emotion/react";
import ClipLoader from "react-spinners/ClipLoader";

export default function Pokemon() {
  const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
`;

  const [pokemon, setPokemon] = useState([])
  const [detailPokemon, setdetailPokemon] = useState([])
  const [loading, setLoading] = useState(true)
  const { name } = useParams();

  const getPokemonData = async () => {
    const pokeData = await fetch(`https://pokeapi.co/api/v2/pokemon/${name}`)
      .then(res => res.json())
    const pokeSpecies = await fetch(`https://pokeapi.co/api/v2/pokemon-species/${name}`)
      .then(res => res.json())

    await setdetailPokemon(pokeSpecies)
    await setPokemon(pokeData)

    setLoading(false)

  }

  useEffect(() => getPokemonData(), [])

  return (
    <>
      {loading ?
        <div className="absolute top-1/2 left-1/2 -translate-y-1/2  -translate-x-1/2">
          <ClipLoader color={'#ffffff'} loading={true} css={css} size={50} />
        </div>
        :
        <Container className="cardFull mb-8">
          <span className="id">#{pokemon.id}</span>
          <Link to="/list" className="absolute top-2 right-2 font-bold">Back</Link>
          <div className="relative w-fit col1">
            <img
              className="w-[220px]"
              src={(pokemon.sprites.other.dream_world.front_default != null) ? pokemon.sprites.other.dream_world.front_default : noFound}
              alt={pokemon.name} />
            <span className="name">{pokemon.name}</span>
            <div className="w-[220px] mt-12 bg-slate-300 rounded-b-md overflow-y-hidden">
              <h3 className="bg-slate-500 p-2 font-bold text-white rounded-t-md ">Description:</h3>
              <p className="p-1 text-slate-700 ">
                {detailPokemon.flavor_text_entries[0].flavor_text}
              </p>
            </div>
          </div>
          <div className="relative p-2 col2">
            <Abilities abilities={pokemon.abilities} type={pokemon.types[0].type.name} />
            <h1 className="title2">Types:</h1>
            <Types types={pokemon.types} />
            <h1 className="my-2 font-bold bg-amber-300 w-fit py-2 px-4 -skew-x-[16deg] rounded-md">Stats</h1>
            <Stats height={pokemon.height} stats={pokemon.stats} />
          </div>
        </Container>
      }
    </>
  );
}