import { Container } from "react-bootstrap";
import { useEffect, useState } from "react";
import { Abilities, Types, Stats } from "./Objects/Const";
import noFound from "./NoFound.png";
import { css } from "@emotion/react";
import ClipLoader from "react-spinners/ClipLoader";

export default function Pokemon() {
  const [pokemon, setPokemon] = useState([]);
  const [detailPokemon, setdetailPokemon] = useState([]);
  const [loading, setLoading] = useState(true);

  const randomizer = () => Math.floor(Math.random() * (898 - 1 + 1)) + 1;
  const [id, setId] = useState(randomizer());

  const getPokemonData = async () => {
    try {
      setLoading(true);
      const res = await fetch(`https://pokeapi.co/api/v2/pokemon/${id}`);
      const pokemon = await res.json();
      const resTxt = await fetch(
        `https://pokeapi.co/api/v2/pokemon-species/${id}`
      );
      const textPokemon = await resTxt.json();
      setPokemon(pokemon);
      setdetailPokemon(textPokemon);
      setLoading(false);
    } catch (error) {
      console.log(error);
    }
  };

  const override = css`
    display: block;
    margin: 0 auto;
    border-color: red;
  `;

  useEffect(() => getPokemonData(), [id]);

  return (
    <>
      {loading ? (
        <div className="absolute top-1/2 left-1/2 -translate-y-1/2  -translate-x-1/2">
          <ClipLoader color={"#ffffff"} loading={true} css={css} size={50} />
        </div>
      ) : (
        <section className="py-4 px-3">
          <Container className="cardFull ">
            <span className="id">#{pokemon.id}</span>
            <div className="relative w-fit col1">
              <img
                className="w-[220px] drop-shadow-[0_0_8px_#ffffff]"
                src={
                  pokemon.sprites.other.dream_world.front_default != null
                    ? pokemon.sprites.other.dream_world.front_default
                    : noFound
                }
                alt={pokemon.name}
              />
              <span className="name">{pokemon.name}</span>
            </div>
            <div className="relative p-2 col2">
              <Abilities
                abilities={pokemon.abilities}
                type={pokemon.types[0].type.name}
              />
              <h1 className="title2">Types:</h1>
              <Types types={pokemon.types} />
              <h1 className="my-2 font-bold bg-amber-300 w-fit py-2 px-4 -skew-x-[16deg] rounded-md">
                Stats
              </h1>
              <Stats height={pokemon.height} stats={pokemon.stats} />
            </div>
            <div className="relative w-full md:w-1/2 mx-auto">
              <button
                className="absolute -bottom-12 left-1/2 -translate-x-1/2  transition-all duration-500 hover:-rotate-90 bg-white rounded-full p-2"
                onClick={() => setId(randomizer())}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-6 w-6"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
                  />
                </svg>
              </button>
              <h3 className="bg-slate-500 px-3 py-4 font-bold text-white rounded-t-md ">
                Description:
              </h3>
              <div className=" bg-slate-300 rounded-b-md ">
                <p className="p-1 h-64 overflow-y-scroll text-slate-700 capitalize">
                  {detailPokemon.flavor_text_entries.map((text) => {
                    return text.language.name === "en"
                      ? text.flavor_text + " "
                      : "";
                  })}
                </p>
              </div>
            </div>
          </Container>
        </section>
      )}
    </>
  );
}
