export const PokemonCard = ({id, name, img, types, abilities}) => {

    const [ data, setData ] = useState(null)

    const type = types[0].type.name;

    return(
    <>
        <Card className="pokemonCard group">
         <Title color={colors[type].bgcolor} name={name}/> 
            <Card.Body className="relative px-[1.3rem] py-3"> 
            <Card.Img variant="top" src={img} className="imgPokemon" style={{backgroundColor:colors[type].color}} />
            <Pokeball/>
            <Number id={id}/>
            <div className="bd" style={{backgroundColor:colors[type].color}}>
                <div className="cirlcle" style={{backgroundColor:colors[type].color}}></div>
                <div className="flex justify-center items-center">
                    <Types types={types}/> 
                </div>
                <Abilities abilities={abilities} type={type}/>
                <Link
                to={`/list/${name}`}
                className="bg-white p-2 rounded-md hover:drop-shadow-[0_0_5px_#ffffff]" 
                style={{color:colors[type].bgcolor}}>
                      Mas informacin
                </Link> 
            </div>           
            </Card.Body>        
        </Card>
        
    </>
    );
}


//others components

export const Abilities = ({abilities, type}) => {
return(
<div className="text-left px-2 py-3 mt-0">
    <span className="bdAbilities">Abilities</span>
    {abilities.map((ability, i) => {
        return(
        <li className="ability" 
        key={i} 
        style={{backgroundColor:colors[type].color}}>
        {i+1}. {ability.ability.name}
        </li>
        )}
        )
    }
</div>  
)}
export const Types =({types}) =>{
    return(
    <>
    {types.map((type,i) => <span key={i} className="bdTitle" style={{backgroundColor:colors[type.type.name].bgcolor, textTransform:"capitalize"}}>{type.type.name}</span>)}
    </>
    );
}
export const Stats =({height, stats})=>{
    return(
        <ol className="text-center">
            <li className="stat">height: <span>{height}</span></li>
            {stats.map((stat, i)=>{
            return(
              <li className="stat" key={i}>{stat.stat.name}:<span> {stat.base_stat}</span></li>
            )
        })}
        </ol>
    );
}



