import  { useState, useEffect, useRef } from 'react'
import { Container } from 'react-bootstrap';
import { PokemonCard } from './Objects/Const';

import { css } from "@emotion/react";
import ClipLoader from "react-spinners/ClipLoader";
import { Input } from 'postcss';

export default function List(){

const inputSearch = useRef()    

const [ pokemons, setPokemons ] = useState([]);
const [ loadMore, setLoadMore ] = useState("https://pokeapi.co/api/v2/pokemon?limit=20")
const [ isloading, setIsLoading ] = useState(true) 

const getPokemons = async () =>{
    try{
        
       const res = await fetch(loadMore);
       const data = await res.json()
       setLoadMore(data.next)
       const promises = data.results.map(pokemon => getDataPokemon(pokemon.name))
       const setingpokemons = await Promise.all(promises)

       setingpokemons.map((pokemon)=>{
            setPokemons(prev => [...prev, pokemon])
            setIsLoading(false)
       })
    }catch(e){}
    async function getDataPokemon(name){
        const res = await fetch(`https://pokeapi.co/api/v2/pokemon/${name}`)
        const data = await res.json();
        return data;
    }
}

useEffect(
    () => getPokemons()
,[])



const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
`;
// console.log(pokemons)
// pokemons.map((pokemon) => console.log(pokemon.abilities))
    return(
    <>
        {(isloading)?
        <div className="absolute top-1/2 left-1/2 -translate-y-1/2  -translate-x-1/2">
        <ClipLoader color={'#ffffff'} loading={true} css={css} size={50} />
        </div>
        :  
        <>
        <section className="p-3">
            <Container className="relative flex items-center justify-center gap-6 flex-wrap">
            {pokemons.map((pokemon, i)=> 
            <PokemonCard
             key={i}  
             id={pokemon.id} 
             name={pokemon.name} 
             img={pokemon.sprites.other.dream_world.front_default} 
             types={pokemon.types} abilities={pokemon.abilities}/>     
            )}
            </Container>
            <div className="text-center py-4">
                <button 
                className="bg-sky-500 p-2 rounded-xl font-bold text-white transition-all duration-500 hover:bg-sky-600"
                onClick={()=> getPokemons()} >
                    Load More
                </button>
            </div>
        </section>
        </>
        }
    </>
    );
}